import AppController from './AppController';
import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor() {
    this.controller = new AppController();
  }

  async showVideos() {
    const urlWithStatistics = await this.controller.getUrlWithStatistic();
    const model = new AppModel(urlWithStatistics);
    const data = await model.getVideosData();
    AppView.drawVideos(data);
  }

  async showNextVideos() {
    const nextPageUrlWithStatistics = await this.controller.getNextPageUrlWithStatistic();
    const model = new AppModel(nextPageUrlWithStatistics);
    const data = await model.getVideosData();
    AppController.changeValueProperty();
    AppView.drawVideos(data);
  }

  start() {
    AppView.drawPage();
    const button = document.querySelector('.btn-search');
    const input = document.querySelector('.form-search');
    button.addEventListener('click', () => {
      AppView.cleanPage();
      this.showVideos();
    });
    input.addEventListener('keyup', (event) => {
      if (event.keyCode === 13) {
        AppView.cleanPage();
        event.preventDefault();
        button.click();
      }
    });

    const elem = document.querySelector('.videos-list');
    elem.addEventListener('scroll', () => {
      const start = elem.scrollLeft;
      elem.scrollLeft = elem.scrollWidth;
      const max = elem.scrollLeft;
      elem.scrollLeft = start;

      if (elem.scrollLeft === max) {
        this.showNextVideos();
      }
    });
  }
}
