export default class AppModel {
  constructor(url) {
    this.url = url;
  }

  static extractVideosData(data) {
    const extractData = str => str.match(/(\d{4}-\d{2}-\d{2})/)[0];

    return data.items.map((clip) => {
      const clipData = {};

      clipData.image = clip.snippet.thumbnails.medium.url;
      clipData.videoId = clip.id;
      clipData.title = clip.snippet.title;
      clipData.channelName = clip.snippet.channelTitle;
      clipData.publishedData = extractData(clip.snippet.publishedAt);
      clipData.description = clip.snippet.description;
      clipData.viewCount = clip.statistics.viewCount;

      return clipData;
    });
  }

  async getVideosData() {
    const responce = await fetch(this.url);
    const data = await responce.json();

    return AppModel.extractVideosData(data);
  }
}
