export default class AppController {
  constructor() {
    this.q = '';
    this.baseLink = 'https://www.googleapis.com/youtube/v3/';
    this.endpoint = 'search';
    this.endpointStatist = 'videos';
    this.apiKey = 'AIzaSyBrg1WD0K5YTRi9XjsdamXd3j1d05Jt3sY';
    this.type = 'video';
    this.part = 'snippet';
    this.partStatist = 'statistics';
    this.maxResults = '15';
    this.nextPageToken = '';
  }

  static extractClipID(data) {
    return data.items.map(clip => clip.id.videoId);
  }

  static extractNextPageToken(data) {
    return data.nextPageToken;
  }

  static async getResponseData(url) {
    const responce = await fetch(url);
    return responce.json();
  }

  getBaseUrl() {
    this.q = document.getElementById('search-video').value;
    return `${this.baseLink + this.endpoint}?key=${this.apiKey}&type=${this.type}&part=${this.part}&maxResults=${this.maxResults}&q=${this.q}`;
  }

  async makeUrlWithStatistics(url) {
    const dataJson = await AppController.getResponseData(url);
    this.nextPageToken = AppController.extractNextPageToken(dataJson);
    const dataId = AppController.extractClipID(dataJson);
    const stringIDs = dataId.join(',');
    const urlWithStatistic = `${this.baseLink + this.endpointStatist}?&key=${this.apiKey}&id=${stringIDs}&part=${this.part},${this.partStatist}`;
    return urlWithStatistic;
  }

  async getUrlWithStatistic() {
    const baseUrl = this.getBaseUrl();
    return this.makeUrlWithStatistics(baseUrl);
  }

  async getNextPageUrlWithStatistic() {
    const nextUrl = `${this.getBaseUrl()}&pageToken=${this.nextPageToken}`;
    return this.makeUrlWithStatistics(nextUrl);
  }

  static changeValueProperty() {
    const videoList = document.querySelector('.videos-list');
    let propertyVideoList = +getComputedStyle(videoList).getPropertyValue('--videos-total');
    propertyVideoList += 15;
    videoList.style.setProperty('--videos-total', `${propertyVideoList}`);
  }
}
