import Slider from './Slider';

export default class AppView {
  static cleanPage() {
    document.querySelector('.videos-list').innerHTML = '';
  }

  static drawPage() {
    const h1 = document.createElement('h1');
    h1.innerHTML = 'Youtube Video';
    document.body.appendChild(h1);

    const searchContainer = document.createElement('div');
    searchContainer.setAttribute('class', 'wrap_form-search');
    document.body.appendChild(searchContainer);

    const input = document.createElement('input');
    input.setAttribute('class', 'form-search');
    input.setAttribute('id', 'search-video');
    input.setAttribute('type', 'search');
    input.setAttribute('placeholder', 'What do you want to find?');
    input.setAttribute('autofocus', 'true');
    searchContainer.appendChild(input);

    const button = document.createElement('button');
    button.innerHTML = 'Search';
    button.setAttribute('class', 'btn-search');
    searchContainer.appendChild(button);

    const contentContainer = document.createElement('div');
    contentContainer.setAttribute('class', 'videos-list');
    document.body.appendChild(contentContainer);
  }

  static drawVideos(data) {
    const content = `
    ${data.map(video => `<div class = "wrap-video-container">
    <ul class="video-container">
      <li class = "video-container_item-media">
          <img class = "item-media_img" src="${video.image}" alt="${video.title}">
          <a class="video_link" href="https://www.youtube.com/watch?v=${video.videoId}" target="_blank">
            ${video.title}
          </a>
      </li>
      <li class = "video-container_item-channelName"><i class = "icon-child"></i>${video.channelName}</li>
      <li class = "video-container_item-data"><i class = " icon-calendar"></i>${video.publishedData}</li>
      <li class = "video-container_item-description"> ${video.description} </li>
      <li class = "video-container_item-viewCount"><i class = "icon-eye"></i>${video.viewCount}</li>
    </ul></div>`).join('')}`;
    document.querySelector('.videos-list').insertAdjacentHTML('beforeend', content);
    Slider.swipe();
  }
}
